
      .right {
        float: right;
        position: relative;
        top: 6px;
      }
      
      .top {
        margin-top: -30px;
      }
      
      .moves-list {
        background: whitesmoke;
        width: 400px;
        padding: 15px;
        border: 1px solid rgba(0, 0, 0, 0.2);
        border-radius: 5px;
        box-shadow: inset 1px 1px 0 white;
        max-height: 450px;
      }
      .moves-list{
        background-color: rgb(0 0 0 / 81%);;
        grid-area: m;
        h1{
            color:#f5f5f5a6;
        }

          ul {
            margin: 0 auto;
            padding: 0;
            max-height: 390px;
            overflow-y: auto;
            border: 1px solid rgba(0, 0, 0, 0.1);
            padding: 5px 5px 0 5px;
            border-left: none;
            border-right: none;
          }
          
          li {
            list-style: none;
            // background-color: rgba(0, 0, 0, 0.05);
            background-color: #f5f5f5a6;
            background-image: 
              linear-gradient(
                90deg,
                #03e9f4 10px,
                #EEE 10px,
                #EEE 11px,
                transparent 11px);
            padding: 10px 15px 10px 25px;
            border: 1px solid #CCC;
            box-shadow: inset 1px 1px 0 rgba(255, 255, 255, 0.5);
            margin-bottom: 5px;
            width: 100%;
            box-sizing: border-box;
            cursor: pointer;
            border-radius: 3px;
          }
    
          
          #moves-list span {
            display: block;
            position: absolute;
            background: #03e9f4;
            bottom: -35px;
            left: -1px;
            width: 360px;
            border-radius: 0 0 5px 5px;
            border: 1px solid rgba(0, 0, 0, 0.1);
            padding: 10px;
            border-top: 1px solid rgba(0, 0, 0, 0.1);
            box-shadow: inset 1px 1px rgba(255, 255, 255, 0.5);
          }
          
      }
      #moves-list select {
        width: 120px;
        margin-left: 230px;
        margin-top: -45px;
        border-radius: 0 3px 3px 0;
        border: 1px solid rgba(0, 0, 0, 0.2);
        border-left: 1px solid rgba(0, 0, 0, 0.1);
        position: absolute;
        padding: 7.5px;
        box-shadow: inset 0 1px 0 rgba(0, 0, 0, 0.1);
      }
      
      #moves-list select:focus, #query:focus {
        border: 1px solid #03e9f4;
        box-shadow: 0 0 10px rgba(255, 255, 0, 0.1);
        outline: none;
      }
